<template>
    <div>
        <div id="s">
            <h3 class="text-center">
                Welcome, Please select your local government
            </h3>
            <br>
            <form action="/plate_n" method="post" class="form" @submit.prevent="save()">
                <div class="col-sm-6 form-group">
                    <label for="local_g" class="sr-only">
                        Select your local Government
                    </label>
                    <select name="status" v-model="userData.lg" class="form-control">
                        <option value="0">Select Local government</option>
                        <template>
                            <option v-for="lg in lgs" :key="lg.id" :value="lg.id">{{lg.name}}</option>
                        </template>
                    </select>
                </div>
                <div class="col-sm-6 form-group">
                    <label for="num" class="sr-only">
                        Enter number of plate numbers
                    </label>
                    <input type="text" class="form-control" placeholder="Number of Plate numbers" v-model="userData.num" required>
                </div>
                <div class="col-sm-6 col-sm-offset-3">
                    <button class="form-control btn bblue">
                        <span class="txt" v-if="!showLoader">Save</span>
                        <span class="spinner" v-else></span>
                    </button>
                </div>
            </form>
        </div>
        <transition enter-active-class="fade-in" leave-active-class="fade-out">
            <div class="modal" v-show="results.length > 0" @click.stop.prevent="modalClicked">
                <div id="results">
                    <table class="table table-bordered table-hover">
                        <thead>
                            <tr>
                                <th>Plate numbers</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="res in results">
                                <td>{{res}}</td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>
        </transition>
    </div>
</template>
<script>

import 'whatwg-fetch'
import Qs from 'qs'
import config from './../config'
export default {
    created() {
        const opts = {
            // mode: 'no-cors'
        }
        fetch(`${this.BASE_URL}/lgs/all`, opts)
            .then(res => this.handleResponse(res))
            .then(res => {
                this.lgs = res
            })
            .catch(err => {
                console.log(err)
                if (!err.body)
                    // Alert error.
                    return this.genericErrorAlert()

                err.body.then(body => {
                    if (body.code == 40) {
                        return body.errors.map(item => {
                            const item_split = item.message.split(':::'),
                                key = item_split[0],
                                msg = item_split[1]

                            this.fields[key].err = true;
                            this.fields[key].msg = msg;
                            this.invalidFields.push(key)
                        })
                    }
                })
            });
    },
    data() {
        return {
            userData: {
                lg: 0,
                num: '',
            },
            lgs: [],
            showLoader: false,
            results: [],
        }
    },
    methods: {
        modalClicked(e){
            console.log(e)
            if (e.target.className == 'modal')
                this.results = []
        },
        save() {
            if (this.userData.lg == 0)
                return alert('Please select a local government.')
            const opts = {
                method: 'POST',
                headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
                body: Qs.stringify(this.userData),
            }

            this.toggleButton()

            fetch(`${this.BASE_URL}/plates/add`, opts)
                .then(res => this.handleResponse(res))
                .then(res => {
                    this.toggleButton()
                    this.results = res
                }).catch(err => {
                    this.toggleButton()
                    console.log(err)
                    if (!err.body)
                        // Alert error.
                        return this.genericErrorAlert()

                    err.body.then(body => {
                        if (body.code == 40) {
                            return body.errors.map(item => {
                                const item_split = item.message.split(':::'),
                                    key = item_split[0],
                                    msg = item_split[1]

                                this.fields[key].err = true;
                                this.fields[key].msg = msg;
                                this.invalidFields.push(key)
                            })
                        }
                    })
                });
        },
        toggleButton() {
            this.showLoader = !this.showLoader
        }
    }

}
</script>

<style scoped>
#s {
    width: 100%;
    max-width: 550px;
    margin: 0 auto;
}

#results {
    width: 100%;
    max-width: 500px;
    margin: 0 auto;
    margin-top: 80px;
    padding: 15px;
    background-color: #fff;
    max-height: 550px;
    overflow: auto;
}

h3 {
    color: #fff;
    padding: 0 15px;
}

@media screen and (max-width: 767px) {
    #results {
        max-height: 450px;
        margin-top: 40px;
    }
}
</style>






