// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.

import Vue from 'vue'
import App from './App'
import router from './router'
import config from './config'

Vue.config.productionTip = false

Vue.mixin({
  data(){
    return {
      baseUrl: 'http://plate.local',
      apiUrl: 'http://plate.local',
      get BASE_URL() {
        return config().base_url
      },
      get AUTH_URL() {
        return config().auth_url
      }
    }
  },
  methods: {
    genericErrorAlert() {
      const msg = 'An error occured.'
      if (navigator.onLine === false)
        return alert(`${msg} Please check your internet connection.`)
      return alert(`${msg} Please try again.`)
    },
    // Custom response interceptor
    handleResponse(res, fn) {
      // Update refresh token if it is returned in the response header
      const refreshToken = res.headers.get('x-refresh-token'),
        accessToken = res.headers.get('access-token')
      if (refreshToken)
        this.$store.commit('saveRefreshToken', refreshToken)
      if (accessToken)
        this.$store.commit('saveAccessToken', accessToken)

      if (fn && typeof fn == 'function')
        fn(res)

      if (res.ok)
        return res.json()

      let e = new Error
      e.status = res.status
      e.headers = res.headers
      try {
        e.body = res.json()
      } catch (e) {
        console.log(e)
      }
      throw e
    },
  }

})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: {
    App
  }
})



